<template>
  <div id="search">
    <div class="search-box-wrapper">
      <search-box ref="searchBox" @query="onQeuryChange"></search-box>
    </div>
    <div class="shortcut-wrapper" v-show="!query" ref="shortcutWrapper">
      <scroll :refreshDelay="refreshDelay" class="shortcut" :data="shortcut" ref="shortcut">
        <div>
          <div class="hot-key">
            <h1 class="title">热门搜索</h1>
            <ul>
              <li class="item" v-for="item in hotkey" @click.prevent="addQuery(item)">
                <span>{{item.k}}</span>
              </li>
            </ul>
          </div>
          <div class="search-history" v-show="searchHistory.length">
            <h1 class="title">
              <span class="text">搜索历史</span>
              <span class="clear" @click="deleteAll">
                <i class="icon-clear"></i>
              </span>
            </h1>
            <search-list @delete="deleteHistory" @select="addQuery" :searches="searchHistory"></search-list>
          </div>
        </div>
      </scroll>
    </div>
    <div class="search-result" ref="searchResult" v-show="query">
      <suggest @select="saveSearch" @listScroll="blurInput" ref="suggest" :query="query"></suggest>
    </div>
    <confirm ref="confirm" :title="title" @confirm="clearSearchHistory"></confirm>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
import SearchBox from '@/base/search-box/search-box'
import {getHotKey} from '@/api/search'
import {ERR_OK} from '@/api/config'
import Suggest from 'components/suggest/suggest'
import Scroll from '@/base/scroll/scroll'
import {mapActions, mapGetters} from 'vuex'
import SearchList from '@/base/search-list/search-list'
import Confirm from '@/base/confirm/confirm'
import {PlayListMixin} from 'common/js/mixin'
export default {
  mixins: [PlayListMixin],
  created() {
    this._getHotKey()
  },
  data() {
    return {
      hotkey: [],
      query: '',
      searchList: [],
      title: '是否清空所有的搜索历史',
      refreshDelay: 100
    }
  },
  computed: {
    shortcut() {
      return this.hotkey.concat(this.searchHistory)
    },
    ...mapGetters([
      'searchHistory'
    ])
  },
  methods: {
    handlePlayList(list) {
      const bottom = list.length > 0 ? '60px' : ''
      this.$refs.shortcutWrapper.style.bottom = bottom
      this.$refs.searchResult.style.bottom = bottom
      this.$refs.shortcut.refresh()
      this.$refs.suggest.refresh()
    },
    addQuery(item) {
      this.$refs.searchBox.setQuery(item.k ? item.k : item)
    },
    deleteHistory(item) {
      this.deleteSearchHistory(item)
    },
    deleteAll() {
      this.$refs.confirm.show()
    },
    onQeuryChange(query) {
      this.query = query
    },
    blurInput() {
      this.$refs.searchBox.blur()
    },
    saveSearch() {
      this.saveSearchHistory(this.query)
    },
    _getHotKey() {
      getHotKey().then((res) => {
        if (res.code === ERR_OK) {
          this.hotkey = res.data.hotkey.slice(0, 10)
        }
      })
    },
    ...mapActions([
      'saveSearchHistory',
      'deleteSearchHistory',
      'clearSearchHistory'
    ])
  },
  components: {
    SearchBox,
    Suggest,
    Scroll,
    SearchList,
    Confirm
  },
  watch: {
    query(newval) {
      if (!newval) {
        setTimeout(() => {
          this.$refs.shortcut.refresh()
        }, 20)
      }
    }
  }
}
</script>

<style lang='stylus' rel='stylesheet/stylus'>
  @import '~common/stylus/variable'
  @import '~common/stylus/mixin'
  #search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-l
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position:relative
          margin: 0 20px
          .title
            display: flex
            align-items:center
            height: 40px
            font-size: $font-size-meidum
            color: $color-text
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
</style>



